import axios, { type AxiosResponse } from 'axios'

import type { ClubId } from 'src/models/Club'

export interface DeleteParameters {
  clubId: ClubId
  token: string
}

export const deleteClubWithdrawal = async ({ clubId, token }: DeleteParameters) => {
  await axios.delete<null, AxiosResponse<null>, null>(`${process.env.REACT_APP_APIHOST}/club/${clubId}/withdrawal`, {
    headers: { Authorization: token }
  })
}
