import { Box, Card, useMediaQuery, useTheme } from '@mui/material'
import { DataGrid, type GridColDef } from '@mui/x-data-grid'

import { type Conference } from 'src/models/Conference'

interface ConferencesWithAttend extends Conference {
  isAttend: boolean
}

interface ConferenceTableProps {
  conferences: ConferencesWithAttend[]
}

const ConferenceTable: React.FC<ConferenceTableProps> = ({ conferences }) => {
  const theme = useTheme()
  const matches = useMediaQuery(theme.breakpoints.up('sm'))

  const sortedConferences = conferences.sort((a, b) => (a.startAt < b.startAt ? 1 : -1))

  const preprocessedConferences = sortedConferences.map(c => ({
    ...c,
    startAt: new Date(c.startAt).toLocaleDateString(),
    isAttend: c.isAttend ? 'O' : 'X'
  }))

  const columns: GridColDef[] = [
    { field: 'title', headerName: '행사명', width: 250 },
    { field: 'startAt', headerName: '시작일', width: 250 },
    { field: 'semester', headerName: '학기', width: 250 },
    { field: 'isAttend', headerName: '출석 여부', width: 100 }
  ]

  const columnsForXS: GridColDef[] = [{ field: 'title', headerName: '행사명', width: 200 }]

  return (
    <Card elevation={0}>
      <Box
        height="55vh"
        sx={{
          '& .MuiTablePagination-toolbar': {
            flexWrap: 'wrap',
            justifyContent: 'center'
          },
          '& .attend--X': { bgcolor: theme.colors.error.lighter }
        }}
      >
        <DataGrid
          rows={preprocessedConferences}
          columns={matches ? columns : columnsForXS}
          initialState={{
            pagination: { paginationModel: { pageSize: window.innerWidth >= 600 ? 10 : 5 } }
          }}
          pageSizeOptions={[5, 10, 25]}
          getRowClassName={params => `attend--${params.row.isAttend}`}
        />
      </Box>
    </Card>
  )
}

export default ConferenceTable
